<template>
	<v-row justify="center">
		<v-dialog
			v-model="open"
			:fullscreen="fullscreen"
			:width="width"
			:transition="fullscreen ? 'dialog-bottom-transition' : 'dialog-transition'"
			@click:outside="closeModal"
		>
			<v-card>
				<v-toolbar
					dense
					flat
					class="toolbar"
					color="primary"
				>
					<v-toolbar-title>
						{{ title }}
					</v-toolbar-title>
					<v-spacer />
					<v-btn
						icon
						@click="closeModal"
					>
						<v-icon>mdi-close</v-icon>
					</v-btn>
				</v-toolbar>
				<slot name="content" />
			</v-card>
		</v-dialog>
	</v-row>
</template>

<script>
export default {
	props: {
		open: {
			type: Boolean,
			default: false
		},
		fullscreen: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: ''
		},
		width: {
			type: String,
			default: undefined
		}

	},
	methods: {
		closeModal() {
			this.$emit('onModalClose', false);
		},
	}
};
</script>

<style lang="scss" scoped>
	.toolbar::v-deep {
		color: white;
		.v-icon {
			color: white;
		}
	}
</style>